.home_wall {
  width: 100%;
}
section {
  background: #0c111b;
  /* height: 250vh; */
  
}
section h1 {
  color: antiquewhite;
  padding-left: 56px;
}
.cards_layout {
  width: 90%;
  margin-left: 60px;
}
.cards {
  width: 321px;
}
.card_img_div {
  overflow: hidden;
  height: 123px;
}
.cards_img {
  width: 100%;
}
.card-container {
  background: linear-gradient(0deg, #1a2b4a 0%, #2b507c 105.38%);
  /* height: 70px; */
  position: relative;
  bottom: 24px;
}
.card-header {
  padding-left: 23px;
  padding-top: 10px;
  color: #fff;
}
.card_wrapper {
  display: grid;
  gap: 16px;
  grid-template-columns: auto auto auto auto;
  justify-content: space-around;
}
.card_rating {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
}
.card_rating-fix {
  display: flex;
  gap: 20px;
}
.card_rating span {
  color: #b3b3b3;
  padding-left: 4px;
}
.star_sty {
  width: 15px;
  height: 15px;
}
.card-play {
  /* width: 50%; */
}
/* button {
	background: none;
