.card-play img {
  background: linear-gradient(180deg, #ff9966 0%, #ff5e62 100%);
  width: 32px;
  height: 32px;
  padding: 5px;
  border-radius: 50px;
  display: flex;
}
.noMovie{
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
}

