/* .item must be first */
 .item {
  align-items: center;
  color: #FF6A63;

  cursor: pointer;
  display: flex;
  font-size: 14px;
  height: 40px;
  justify-content: center;
  width: 40px;
}

.disabled-page {
  color: #808e9b;
}

.active {
  border: solid 1px #808e9b;
  border-radius: 40px;
  color: #808e9b;
}

.break-me {
}

.next {
  /* border-left: solid 1px #808e9b; */
  font-size: 4px;
  height: 60px;
  /* position: absolute; */
  right: 0;
  /* width: 150px; */
}
.pagiFoot{
  margin-left: 60px;
}
 .pagination {
  align-items: center;
  background: #0c111b;
  display: flex;
  flex-direction: row;
  height: 70px;
  justify-content: center;
  list-style-type: none;
  position: relative;
  width:auto;
  padding: 10px;
}

.pagination-page {
  font-weight: 700;
}

.previous {
  font-size: 4px;
  height: 60px;
  left: 0;
}
@media only screen and (min-width: 320px) and (max-width: 481px) {
  .card_wrapper {
    grid-template-columns: auto;
  }
  .cards_layout {
    width: 0%;
    padding-left: 120px;
  }
  section {
    /* height: 820vh; */
  }
}
/* Media Query for Tablets Ipads portrait mode */
@media (min-width: 768px) and (max-width: 1024px) {
  .card_wrapper {
    grid-template-columns: auto auto;
  }
}

@media (min-width: 1025px) and (max-width: 1280px) {
  .card_wrapper {
    grid-template-columns: auto auto;
  }
}
